function NotFound({navigate}) {
  return (
    <div style={{padding: "90px 0 300px 0"}}>
      <div style={{fontSize: "22px", fontWeight: "600"}}>
           .</div>
      <div style={{
        textDecoration: "underline",
        color: "#2A6834", 
        marginTop: "20px",
        cursor: "pointer"
      }} 
        onClick={() => navigate('/')}> </div>
    </div>
  )
};

export default NotFound;