import React, { useState } from "react";
import styled from "styled-components";
//components
import Card from "./Card";
import Timer from "./timer";


function Tab({ items, navigate }) {

  const [currentTab, clickTab] = useState(0);

  /*    */
  const timeItems = items.filter(data => data.detail_menu === "");
  const limitedItems = items.filter(data => data.detail_menu === "");
  const onlyItems = items.filter(data => data.detail_menu === "");

  /*    index  */
  const handleTabClick = (index) => {
    clickTab(index);
  };

  const tabArr = [
    /*  */
    {
      tabButton: (
        <li className={currentTab === 0 ?
          "tabButton focused" : "tabButton"}
          onClick={() => handleTabClick(0)}>
          
        </li>
      ),
      tabItems: (
        <>
          {/*  */}
          <TimerWrap>
            < Timer hh="24" mm="00" ss="00" />
            <p> 24  !</p>
          </TimerWrap>
          <TapContainer>
            {timeItems.map((data, i) => (
              <div key={i} className="card-link"
                onClick={() => navigate(`/detail/${data.id}`)}>
                <Card data={data}></Card>
              </div>
            ))}
          </TapContainer>
        </>
      )
    },
    /*  */
    {
      tabButton: (
        <li className={currentTab === 1 ?
          "tabButton focused" : "tabButton"}
          onClick={() => handleTabClick(1)}>
          
        </li>
      ),
      tabItems: (
        <>
          <TxtWrap>
            <p>200 </p>
            <p>    !</p>
          </TxtWrap>
          <TapContainer>
            {limitedItems.map((data, i) => (
              <div key={i} className="card-link"
                onClick={() => navigate(`/detail/${data.id}`)} >
                <Card data={data}></Card>
              </div>
            ))}
          </TapContainer>
        </>
      )
    },
    /*  */
    {
      tabButton: (
        <li className={currentTab === 2 ?
          "tabButton focused" : "tabButton"}
          onClick={() => handleTabClick(2)}></li>
      ),
      tabItems: (
        <>
          <TxtWrap>
            <p>20%~50%</p>
            <p> !   !</p>
          </TxtWrap>
          <TapContainer>
            {onlyItems.map((data, i) => (
              <div key={i} className="card-link"
                onClick={() => navigate(`/detail/${data.id}`)} >
                <Card data={data}></Card>
              </div>
            ))}
          </TapContainer >
        </>
      )
    }];

  return (
    <>
      <TabBtnWrap>
        {tabArr.map((data, i) => 
          <TabBtn key={i}>{data.tabButton}</TabBtn>
        )}
      </TabBtnWrap>
      <div className="tap_menu">
        {tabArr[currentTab].tabItems}
      </div>

    </>
  )
};


export default Tab;

const TabBtnWrap = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 30px;
`
const TabBtn = styled.ul`
  display: flex;
  justify-content: center;
  .tabButton {
    display: flex;
    margin: 0 5px;
    padding: 8px 14px;
    font-size: 14px;
    border-radius: 30px;
    background-color: rgba(42, 104, 52, 0.25);
    cursor: pointer;
    transition: .2s;
    &:hover {
      background-color: rgba(42, 104, 52, 0.45);
      color: #2A6834;
    }
  }
  .focused {
    background: #2A6834;
    color: #fff;
    &:hover {
      background: #2A6834;
      color: #fff;
    }
  }
`
const TapContainer = styled.div`
position: relative;
display: flex;
flex-wrap: wrap;
justify-content: center;
align-items: flex-start;
gap: 20px;
margin-top: 30px;
.card-link {
  position: relative;
  cursor: pointer;
}
@media(max-width: 1229px) {
  flex-wrap: nowrap;
  justify-content: flex-start;
}
`
const TimerWrap = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  p {
    color: #b1b1b1;
    font-size: 14px;
  }
`
const TxtWrap = styled.div`
  p:first-child {
    font-weight: 600;
    font-size: 20px;
    margin-top: 30px;
  }
  p:last-child {
    font-size: 14px;
    color: #b1b1b1;
    margin-top: 10px;
  }
`