import "./style.css"
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
//redux
import { addItem, increase } from "../../store/cartSlice";
//component
import CommaFormat from "../../components/CommaFormat";
import CartModal from "../../components/cart/CartModal";
//mobile
import MobDetailHeader from "../../components/mobile/mobDetailHeader";
import DetailBottomNav from "../../components/mobile/bottomNav/detailBottomNav";
//responsive
import { Pc, Mobile } from "../../components/mobile/responsive";


function DetailPage({ items }) {

  /* url  */
  const { id } = useParams();

  /* items id url   */
  const findItem = items.find((data) => data.id == id);

  /* redux */
  const state = useSelector((state) => state);
  const cartData = state.cart;

  const dispatch = useDispatch();

  const [count, setCount] = useState(1);
  const [modal, setModal] = useState(false);

  /* redux findIndex id  index  */
  const indexFind = cartData.findIndex((i) => { return i.id === findItem.id });
  const findItemId = findItem.id;

  /*   , redux   */
  const sameItemFind = () => {
    indexFind < 0 ?
      dispatch(addItem({
        id: findItem.id,
        name: findItem.title,
        price: findItem.price,
        image: findItem.image,
        count: count,
        checked: true,
      }))
      : dispatch(increase(findItemId))
  };

  /*    */
  const onIncrease = () => {
    setCount(count + 1)
  };
  const onDecrease = () => {
    count === 1 ? alert('1   ')
      : setCount(count - 1)
  };

  /* 3    */
  useEffect(() => {
    setTimeout(() => { setModal(false) }, 3000)
  });

  return (
    <>
      <Mobile>
        {/*     */}
        <MobDetailHeader findItem={findItem} />
      </Mobile>
      <div className="detail-container">
        {/*   */}
        <div className="detail-image">
          <img src={process.env.PUBLIC_URL + findItem.image} />
        </div>
        <div className="item-info-wrap">
          {/*   */}
          <div className="title-wrap">
            <h4 className="detail-title">{findItem.title}</h4>
            <p className="discount-rate">{findItem.discount_rate}</p>
            <p>{CommaFormat(findItem.price)}<span className="won"></span></p>
            <p className="detail-dimmed-price">{findItem.dimmed_price}</p>
          </div>
          {/*   */}
          <dl className="item-info">
            <div>
              <dt></dt>
              <dd>{findItem.sales_unit}</dd>
            </div>
            <div>
              <dt>/</dt>
              <dd>{findItem.net}</dd>
            </div>
            <div>
              <dt></dt>
              <dd>/ </dd>
            </div>
            <div>
              <dt></dt>
              <dd> , 1     </dd>
            </div>
            <Pc>
              <div>
                <dt></dt>
                <dd>
                  <div className="counter">
                    <button onClick={onDecrease}>-</button>
                    <p>{count}</p>
                    <button onClick={onIncrease}>+</button>
                  </div>
                </dd>
              </div>
            </Pc>
          </dl>
          <Pc>
            {/*     */}
            <div className="amount-wrap">
              <div className="amount-price">
                <span className="amount-title">  : </span>
                {CommaFormat(count * findItem.price)}<span className="won"></span>
              </div>
              {/*    */}
              <button className="cart-btn"
                onClick={() => {
                  sameItemFind()
                  setModal(true)
                }}> </button>
              {modal === true ? <CartModal /> : null}
            </div>
          </Pc>
        </div>
      </div>
      <Mobile>
        {/*    ( ) */}
        <DetailBottomNav
          sameItemFind={sameItemFind}
          items={items}
          modal={modal}
          setModal={setModal}
        />
      </Mobile>
    </>
  )
};

export default DetailPage;